.opbody{
    color: white;
}
.opcenter{
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 60px;
}
.optext{
    font-family: monospace;
    font-size: 30px;
    color: black;
    display: block;
    text-align: center;
}

.opwrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white
}

.opcheckmark__circle {
    stroke-dasharray: 166;
    stroke-dashoffset: 166;
    stroke-width: 2;
    stroke-miterlimit: 10;
    stroke: #7ac142;
    fill: none;
    animation: stroke 0.6s cubic-bezier(0.65, 0, 0.45, 1) forwards
}

.opcheckmark {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    display: block;
    stroke-width: 2;
    stroke: #fff;
    stroke-miterlimit: 10;
    margin: 2% auto;
    box-shadow: inset 0px 0px 0px #7ac142;
    animation: fill .4s ease-in-out .4s forwards, scale .3s ease-in-out .9s both
}

.opcheckmark__check {
    transform-origin: 50% 50%;
    stroke-dasharray: 48;
    stroke-dashoffset: 48;
    animation: stroke 0.3s cubic-bezier(0.65, 0, 0.45, 1) 0.8s forwards
}

@keyframes stroke {
    100% {
        stroke-dashoffset: 0
    }
}

@keyframes scale {

    0%,
    100% {
        transform: none
    }

    50% {
        transform: scale3d(1.1, 1.1, 1)
    }
}

@keyframes fill {
    100% {
        box-shadow: inset 0px 0px 0px 30px #7ac142
    }
}

.opbutton1 { background-color: #4CAF50; /* Green */ border: none; color: white;
    padding: 15px 32px; text-align: center;display:block;
    margin-left:auto;margin-right:auto;display:block;margin-top: 1%;
    font-size: 15px;
   

}